import React, { useState, useEffect } from 'react'
import ArticlesList from '../Components/ArticlesList'
import CommentsList from '../Components/CommentsList';
import UpvotesSection from '../Components/UpvotesSection'
import AddComment from '../Components/AddComment'
import NotFoundPage from './NotFoundPage'
import { sampleArticles } from '../sample-articles'

function ArticlePage(props) {

    const name = props.match.params.name;

    const [articleInfo, setArticleInfo] = useState({
        upvotes: 0,
        comments: []
    })

    useEffect(() => {

        const fetchData = async () => {

            const response = await fetch(`/api/articles/${name}`);
            const body = await response.json();

            setArticleInfo(body);

        }

        fetchData();

    }, [name])

    const article = sampleArticles.find(article => article.name === name);

    if (!article)
        return <NotFoundPage />

    const relatedArticles = sampleArticles.filter(article => article.name !== name);

    return (
        <>
            <h1> {article.title} </h1>

            <UpvotesSection 
                articleName={name}
                upvotes={articleInfo.upvotes}
                setArticleInfo={setArticleInfo}
            />

            {
                article.content.map((p, i) => <p key={i}> {p} </p>)
            }

            {
                !!articleInfo.comments.length &&
                <CommentsList comments={articleInfo.comments} />
            }

            <AddComment
                articleName={name}
                setArticleInfo={setArticleInfo}
            />

            <h2> Related Articles </h2>
            <ArticlesList articles={relatedArticles} />

        </>
    )
}

export default ArticlePage;